import React, { useEffect, useState, useMemo, Fragment } from "react";
import { Switch, Route } from "react-router-dom";
import LoadingButton from "@mui/lab/LoadingButton";
import CircularProgress from "@mui/material/CircularProgress";
import BigNumber from "bignumber.js";
import {
  Dialog,
  DialogTitle,
  DialogContent,
  DialogContentText,
  TextField,
  DialogActions,
} from "@material-ui/core";
import MuiAlert, { AlertProps } from "@mui/material/Alert";
import Snackbar from "@mui/material/Snackbar";
import { RootState, RootDispatch } from "./model";
import { useRematch } from "./utils/useRematch";
import BoxMovie from "./assets/box.mp4";
import Home from "./pages/Home";
import "./App.css";

const mapState = (state: RootState) => ({
  ...state.main,
  loading: state.loading,
});

const mapDispatch = (dispatch: RootDispatch) => ({
  ...dispatch.main,
});

const Alert = React.forwardRef<HTMLDivElement, AlertProps>(function Alert(
  props,
  ref
) {
  return <MuiAlert elevation={6} ref={ref} variant="filled" {...props} />;
});

function App() {
  const [
    { isMainNet, alertState, showMintModal, loading },
    { init, setFields, openAlert, mint, closeAlert },
  ] = useRematch(mapState, mapDispatch);

  useEffect(() => {
    // init();
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  const [mintNumber, setMintNumber] = useState("1");

  const MAX_MINT_NUMBER_PRESALES = 5;
  const MAX_MINT_NUMBER_SALES = 20;

  const mintNumberInvalid = useMemo(() => {
    const mintNumberBignumber = new BigNumber(mintNumber);
    return (
      mintNumberBignumber.isNaN() ||
      !mintNumberBignumber.isInteger() ||
      mintNumberBignumber.isZero() ||
      mintNumberBignumber.isGreaterThan(MAX_MINT_NUMBER_PRESALES)
    );
  }, [mintNumber]);

  return (
    <div className="App">
      <Snackbar
        anchorOrigin={{ vertical: "top", horizontal: "center" }}
        open={!isMainNet}
      >
        <Alert severity="error" sx={{ width: "100%" }}>
          You are not connect to the Ethereum network!
        </Alert>
      </Snackbar>
      <Snackbar
        anchorOrigin={{ vertical: "top", horizontal: "center" }}
        open={alertState.open}
        autoHideDuration={6000}
        onClose={closeAlert}
      >
        <Alert severity={alertState.type} onClose={closeAlert}>
          {alertState.content}
        </Alert>
      </Snackbar>
      <Dialog
        open={showMintModal}
        onClose={() => {
          setFields({
            showMintModal: false,
          });
        }}
      >
        <DialogTitle
          style={{
            background: "#001417",
            textTransform: "uppercase",
            display: "flex",
            alignItems: "center",
          }}
          onClick={() => {
            openAlert({
              type: "success",
              content: "Congratulations! Transaction Confirmed!",
            });
          }}
        >
          {loading.effects.main.mint ? (
            <Fragment>
              PENDING...
              <CircularProgress size="24px" style={{ marginLeft: "20px" }} />
            </Fragment>
          ) : (
            "Mint your own BOTHEAD"
          )}
        </DialogTitle>
        <DialogContent style={{ background: "#001417" }}>
          {loading.effects.main.mint ? (
            <div className="banner">
              <video
                autoPlay
                loop
                muted
                width="100%"
                className="banner-img"
                preload="auto"
              >
                <source src={BoxMovie} type="video/mp4" />
              </video>
            </div>
          ) : (
            <Fragment>
              <DialogContentText style={{ marginBottom: "24px" }}>
                <b>PRE MINT MAX MINT NUMBER: {MAX_MINT_NUMBER_PRESALES}</b>
                {/* <b>MAX MINT NUMBER: {MAX_MINT_NUMBER_SALES}</b> */}
              </DialogContentText>
              <DialogContentText style={{ marginBottom: "24px" }}>
                After your successfully mintting, after a few minutes, you can
                see your BOTHEAD NFT on OpenSea.
              </DialogContentText>
              <TextField
                autoFocus
                error={mintNumberInvalid}
                margin="dense"
                type="text"
                value={mintNumber}
                onChange={(e) => {
                  setMintNumber(e.target.value);
                }}
                label={`Mint Number`}
                fullWidth
                variant="standard"
                helperText={mintNumberInvalid ? "Number invalid." : ""}
              />
            </Fragment>
          )}
        </DialogContent>
        <DialogActions style={{ background: "#001417" }}>
          <LoadingButton
            loading={loading.effects.main.mint}
            loadingIndicator="Pending"
            variant="text"
            disabled={loading.effects.main.mint || mintNumberInvalid}
            onClick={() => {
              mint(new BigNumber(mintNumber).toNumber());
              // mint(new BigNumber(mintNumber).toNumber());
            }}
          >
            Mint
          </LoadingButton>
        </DialogActions>
      </Dialog>
      <Switch>
        <Route path="/" component={Home} />
      </Switch>
    </div>
  );
}

export default App;
